export const TextInputComponent = props => {
  const { id = '', error = '', value, onChange } = props
  return (
    <React.Fragment>
      <InputBase
        value={value}
        type='text'
        onChange={onChange}
        className={error && !value ? classes.inputFieldsError : classes.inputFields}
        autoComplete='off'
      />
    </React.Fragment>
  )
}

export const NumberInputComponent = props => {
  const { id = '', error = '', disabled = false, value, onChange, format = null, onValueChange = true } = props
  const HandleChange = (newValue) => {
    return onValueChange ? onChange(newValue.formattedValue) : onChange(newValue.value)
  }

  return (
    <React.Fragment>
      <NumberFormat
        id={id}
        className={error && !value ? classes.inputFieldsError : classes.inputFields}
        disabled={disabled}
        format={format}
        value={value}
        onValueChange={values => HandleChange(values)}
        decimalSeparator={false}
        customInput={InputBase}
        isNumericString
        allowLeadingZeros
        allowNegative={false}
        displayType={'input'}
      />
    </React.Fragment>
  )
}
